}

.clip-hex-reverse {
  /* clip-path: polygon(80% 0, 100% 25%, 100% 100%, 19% 100%, 0 76%, 0 0); */
  clip-path: polygon(
    0 0,
    /* top-left corner */ 86% 0,
    /* move inward from right side instead of left */ 100% 13%,
